// Sprites
@import "sprite/icons";
@import "sprite/iconsm";

// Variables

$brightBg: #019993;
$modalHead: #00a19a;
$modalRadius: 10px;
$text: #464646;

$bigButtonsHeight: 40px;
$bigButtonsRounded: 7px;
$secondaryBorder: #e95849;

$loginFormColor: #919191;

$submitBg: #fd5f42;
$linkReg: #60aa24;

$buttonText: #e74c3c;

$contactsAcceptWidth: 285px;

$xs: 320px;
$s: 480px;
$m: 768px;
$l: 960px;
$xl: 1220px;

$phone-1: 153px;
$phone-2: 188px;
$phone-3: 227px;

// Mixins

@mixin phoneShadow {
  position: absolute;
  left: 15px;
  top: 15px;
  bottom: 15px;
  right: 15px;
  z-index: -1;
  display: block;
  content: "";
  border-radius: 30px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}

// Styles

body {
  font-family: "Century Gothic", sans-serif;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
}

.logo__img {
  width: 257px; 

  @media (max-width: $l - 1) {
    width: 190px;
  }

  @media (max-width: $m - 1) {  
    margin: 10px auto;
    display: block;  
  }

  &_footer {
    width: 90px;
  } 
}

.sp-icons,
.sp-iconsm {
  display: block;
}

.uk-button-large {
  height: $bigButtonsHeight;
  border-radius: $bigButtonsRounded;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: bold;

  @media (max-width: $l - 1) {
    height: $bigButtonsHeight * 0.8;
    padding-left:  15px;
    padding-right:  15px;
  }

  &_footer {
    height: auto;
    font-size: 16.5px;
    padding: 15px 36px;
    margin-bottom: 0;
  }
}

.uk-button {
  &_full {
    font-size: 14px;
    width: 100%;
    height: 40px;
    margin: 10px 0;
  }
}

.uk-button-secondary {
  border: 1px solid $secondaryBorder;
}

.header {
  @media (max-width: $m - 1) and (orientation: portrait) {
    display: block; 
  }
}

.header-nav {
  @media (max-width: $m - 1) and (orientation: portrait) {
    display: block;
  }
}

.logo-title {
  margin-bottom: 0;

  @media (max-width: $m - 1) and (orientation: portrait) {
    display: block; 
  }

}

.logo {
  display: block;
  margin: 5px 15px 5px 0px;
  font-size: 45px;

  @media (max-width: $l - 1) {
    margin-left: 20px;
  } 

  @media (max-width: $m - 1) and (orientation: portrait) {
    margin: 10px auto;
  }

  @media (max-width: $m - 1) and (min-width: $s) and (max-height: $s - 1) and (orientation: landscape) {
    margin-left: 5px;
  }

  &_footer {
    font-size: 16px;
    margin: 0px 15px 0 15px;

    @media (max-width: $m - 1) and (orientation: portrait) {
      margin: auto;
      margin-bottom: 10px!important;
    }
  }
}

.how-it-works {
  background: $brightBg;
}


.choose-lang-block {  
  margin-right: 45px;

  @media (max-width: $l - 1) {
    margin-right: 25px;
  }

  @media (max-width: $m - 1) {
    margin-right: 0;
    width: 100%;
  }
}

.choose-lang {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;

  &:hover,
  &:active,
  &:focus {
    background-color: transparent;
    border: none;
    outline: none;
  }

  &__inner {
    padding: 7px 9px;
    border: 1px solid #dfdfdf;
    border-radius: 3px;
    background-color: white;
  }

  &:hover {
    cursor: pointer;

    .choose-lang__text {
      color: lighten($text, 20%);
    }
  }

  &__arr {
    margin-left: 4px;
    margin-right: 4px;
  }

  @media (max-width: $m - 1) {
    width: 100%;
  }
}

.auth-buttons {
  .auth-button {
    margin-right: 15px;

    &_last {
      margin-right: 0;
    }

    @media (max-width: $m - 1) {
      align-items: center;
      margin: 20px 0;
    }
  }


  @media (max-width: $m - 1) and (orientation: portrait) {
    justify-content: center;

    .auth-button {
      margin-right: 0!important;
      margin-top: 10px;
      width: 50%;
      max-width: 162px;

      .uk-button {
        width: 100%;
      }
    }
  }

}

.button {
  &_login {
    background-color: transparent;
    font-size: 13px;
    font-weight: 700;
    color: $buttonText;
    text-transform: uppercase;
    border: none;
    margin-left: 20px;

    &:hover {
      background-color: transparent;
      color: lighten($buttonText, 20%);
    }

    @media (max-width: $m - 1) {
      margin-left: 0;
    }
  }
}

.uk-panel {
  border: 2px solid #acdddb;
  border-radius: 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  padding: 25px 20px;
  box-sizing: border-box;

  &_how {
    background-color: #7fb7ab;
    margin: 40px 20px;
    max-width: $contactsAcceptWidth;
    padding: 35px;

    .uk-panel-title {
      text-transform: uppercase;
      margin-bottom: 10px;
    } 

    @media (max-width: $l - 1) {
      max-width: 460px;

      .uk-panel-title {
        text-transform: uppercase;
        min-width: 0;
        margin: 0;
      } 
    }

    @media (max-width: $l - 1) {
      max-width: none;
      margin: 40px auto 0;
      padding: 30px 20px;
    }

    @media (max-width: $m - 1) {
      border-width: 3px;
    }

    @media (max-width: $m - 1) and (orientation: portrait) {
      margin: 20px 0;
      padding-bottom: 20px;
    }

    @media (max-width: $s - 1) {
      margin: 12px -8px;
    }
  }

  &__column {
    @media (max-width: $m - 1) and (min-width: $s) and (max-height: $s - 1) and (orientation: landscape) {
      float: left;
      width: 50%;
    }
  }
}

.uk-panel-title {
  font-family: "Century Gothic", sans-serif;
  font-weight: bold;
  font-size: 15px;
  margin-bottom: 15px;

  @media (max-width: $m - 1) {
    font-size: 18px;
  }
}

.uk-panel-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;

  &_footer {
    margin-bottom: 0;
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 6px 0;

    @media (max-width: $m - 1) {
      font-size: 18px;
    }
  }

  &__i {
    flex-shrink: 0;
  }

  &__link {
    color:white;
    padding-left: 5px;
    text-decoration: underline;
    transition: 0.5s;

    &:hover {
      color:white;
      opacity: 0.5;
    }
  }
}

.uk-button-small {
  border-radius: 30px;
  padding: 10px 0;
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
}

.uk-button_how {
  width: 200px;
  white-space: nowrap;
  margin-bottom: 25px;
  padding-top: 13px;
  padding-bottom: 13px;
}

.how-container {

}

.phone-img-cont {
  display: flex;
  margin: 55px 0;

  @media (max-width: $s - 1) {
    flex-direction: column;
    align-items: center;
  }
}


.how-accept {
  max-width: $contactsAcceptWidth;
  padding: 15px 0;
  color:white;
  font-size: 15px;
  font-weight: bold;

  @media (max-width: $xl - 1) {
    flex-direction: column;
  }

  &__row {
    margin: 15px 10px;
    text-align: center;

    @media (max-width: $s - 1) {
      margin: 15px 0px;
    }

    &_column {
      @media (max-width: $s - 1) {
        flex-direction: column;
      }
    }

  }

  &__title {    
    text-transform: uppercase;
    margin: 10px auto 10px;
    text-align: center;

    @media (max-width: $s - 1) {
      margin-bottom: 10px;
      margin-right: 0;
    }
  }

  &__country {
    margin-right: 3px;

    @media (max-width: $m - 1) {
      margin-right: 6px;
    }
  }


  @media (max-width: $m - 1) {
    font-size: 18px;
    margin: auto;
  }

  @media (max-width: $m - 1) and (orientation: portrait) {
    display: none; 
  }

  @media (max-width: $m - 1) and (max-height: $s - 1) and (orientation: landscape) {
    display: block;
  }

}

.mobile-column {
  @media (max-width: $m - 1)  and (max-height: $s - 1) and (orientation: landscape) {
    display: block;
  }
}

.sp-icons {

  &_margin {
    margin: 0 5px;
  }
}

.title {
  font-size: 40px;
  font-weight: 700;
  margin-top: 50px;
  line-height: 45px;

  @media (max-width: $m - 1) {
    font-size: 26px;
    margin-top: 40px;
    line-height: 35px;

  }

  &_modal {
    font-size: 24px;
    font-weight: 500;
    color: white;
    text-align: center;
    background-color: $modalHead;
    border-radius: $modalRadius $modalRadius 0 0;
    padding: 5px;
    margin-bottom: 30px;
    margin-top: 0;
  }
}

.devider {
  margin: 25px;
}

.advant-list {
  list-style-type: none;
  max-width: 1050px;
  padding: 0;
  margin: 55px auto 80px;

  @media (max-width: $m - 1) {
    flex-direction: column;
    align-items: center;
  }

  &__item {
    text-align: center;
    font-weight: bold;
    font-size: 13.5px;
    margin-bottom: 30px;
    padding: 0 10px;
    box-sizing: border-box;

    @media (min-width: $m) {
      max-width: 20%;
      margin-bottom: 0px;
    }
  }

  &__img {
    margin-bottom: 30px;

    @media (max-width: $m - 1) {
      margin-bottom: 10px;
    }

    .sp-icons {
      margin: auto;
    }
  }
}

.for-titles-title {
  color: white;
  width: 100%;
  font-size: 20px;
  text-align: center; 
  margin-bottom: 50px;

  &__text {
    padding-bottom: 4px;
    border-bottom: 2px solid white;
  }

  @media (max-width: $xl - 1) {
    font-size: 16px;
    margin-bottom: 30px;
  }

  @media (max-width: $l - 1) {
    font-size: 13px;
    margin-bottom: 10px;
  }

  @media (max-width: $m - 1) {
    margin: 20px;
    position: static;
  }
}

.phone-box {
  z-index: 1;
  width: 100%;

  &__main {    
    width: 235px;

    @media (max-width: $xl - 1) {
      width: 215px;
    }

    @media (max-width: $l - 1) {
      width: 175px;
    }
  }

  &__main-img {
    width: 100%;
  }
}

.phone-box-side {
  position: absolute;
  top: 0;
  z-index: -1;
  width: 230px;

  @media (max-width: $xl - 1) {
    width: 190px;
  }

  @media (max-width: $l - 1) {
    width: 160px;
  }

  &__item {
    height: 150px;

    @media (max-width: $m - 1) {
      margin-top: 10px;
      margin-bottom: 10px;
    }

    @media (max-width: $xl - 1) {
      height: 135px;
    }

    @media (max-width: $l - 1) {
      height: 115px;
    }
  }

  &__i {
    position: relative;
    margin: auto;

    &::before {
      position: absolute;
      display: block;
      content: "";
      background-color: transparent;
      background-image: radial-gradient(rgba(255, 255, 255, .39) 47%, transparent 42%);
      background-size: 10px 10px;
      background-position: left center;
      width: 100px;
      height: 12px;
      left: 50%;
      margin-left: 30px;
      top: 40%;

      @media (max-width: $xl - 1) {
        width: 70px;
      }

      @media (max-width: $l - 1) {
        width: 60px;
      }
    }
  }

  &_left {
    .phone-box-side__text {

      @media (max-width: $l) {
      }

      @media (max-width: $m - 1) {
      }
    }
  }

  &_right {
    right: 0;

    .phone-box-side__i {

      &::before {
        right: 50%;
        left: auto;
        margin-left: auto;
        margin-right: 30px;
      }
    }
  }

  &__text {
    color: white;
    max-width: 180px;
    margin: 10px auto 0;
    font-size: 13px;
    line-height: 15px;
    text-align: center;

    @media (max-width: $l - 1) {
      max-width: 140px;
      font-size: 12px;
      margin-top: 5px;
    }
  }

}

.reg-button-container {
  display: flex;

  &_only-reg {
    justify-content: center;
  }

  &__button {
    margin-top: 50px;
    margin-bottom: 70px;
  }
}

.example {
  background-color: $text;
  padding-bottom: 40px;

  &__title {
    color: white;
  }
}

.example-list {
  background: url("../img/dots_bg.png") center 90px no-repeat;
  background-size: 940px;

  @media (max-width: $xl - 1) {
    background-size: contain;
  }

  @media (max-width: $l - 1) {
    background-image: none;
  }

  &__row {

    @media (max-width: $l - 1) {
      flex-direction: column;
      align-items: center;
    }

    &_second {

      .example-list__item {
        @media (min-width: $l) {
          margin-top: 70px;
        }

        &_penultimate {
          @media (min-width: $l) {
            margin-top: 45px;
          }
        }

        &_last {
          width: 190px;
          height: 190px;
          color: white;
          border-radius: 18px;

          @media (min-width: $l) {
            margin: 0 130px 10px -30px;
          }
        }
      }
    }
  }

  &__item {
    max-width: 350px;
    text-align: center;
    margin: 25px;
    margin-top: 40px;

    @media (min-width: $l) {
      max-width: 240px;
    }

    &_last {
      background: #6f99b5;
      padding: 10px;
      box-sizing: border-box;
    }
  }

  &__text {
    color: white;
    font-size: 13px;
    line-height: 15px;

    &_big {
      text-transform: uppercase;
      font-weight: bold;
    }
  }

  &__number {
    font-family: "Bitter", sans-serif;
    font-size: 28px;
    margin: auto auto 15px;
    color: white;
  }
}

.look {
  color: #00a40c;
}


.uk-button-mini {
  border-radius: 25px;
  width: auto;
  padding: 13px 33px;
  font-weight: bold;
  font-size: 13.5px;
  text-transform: uppercase;
  margin: 0;

  @media (max-width: $s - 1) {
    padding: 13px 23px;
  }
}

footer {
  background: $brightBg;
}

.sup-footer {
  padding: 50px 0;

  .uk-panel-list__item {
    @media (max-width: $m - 1) {
      justify-content: center;
      flex-direction: row;
    }
  }

  &__contacts {
    @media (max-width: $m - 1) {
      order: 1;
    }

    @media (min-width: $m) {
      width: 33.3%
    }
  }

  &__button-reg {   
    text-align: center;
    
    @media (max-width: $m - 1) {
      margin-bottom: 20px;
      order: 1;
    }

    @media (min-width: $m) {
      width: 33.3%;
    }
  }

  &__reg-button {
    display: inline-block;
  }

  &__wm {
    justify-content: center;

    @media (min-width: $m) {
      justify-content: flex-end;
    }

    @media (max-width: $m - 1) {
      padding: 0;
      margin-top: 20px;
    }
    
    @media (min-width: $m) {
      width: 33.3%
    }
  }

  @media (max-width: $m - 1) {
    flex-direction: column-reverse;
  }
}

.sub-footer {
  background: white;

  &__inner {
    padding: 10px 0;

    @media (max-width: $m - 1) {
      flex-direction: column;
    }
  }

  &__logo {
    justify-content: center;
  }
}

.copyright {
  font-size: 15px;

  @media (max-width: $m - 1) and (orientation: portrait) {
    margin: 20px;
  }
}


// Login/Register

.auth-form {
  width: 285px;
  padding-top: 15px;
  margin-top: 15px;
  background: white;
  border-radius: 2px;
  font-family: Arial, sans-serif;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.3), 0 0 1px rgba(0, 0, 0, 0.2);

  &__lang {
    width: 200px;
    padding: 15px;
  }

  &::before {
    position: absolute;
    display: block;
    content: "";
    top: -5px;
    right: 50%;
    margin-right: -5px;
    border-top: 5px solid white;
    border-left: 5px solid white;
    border-bottom: 5px solid transparent;
    border-right: 5px solid transparent;
    box-shadow: -1px -1px 1px rgba(0,0,0,.1);
    transform: rotate(45deg);
  }

  &__title {
    font-size: 16px;
    margin: 0 15px 10px;
  }

  &__icon {    
    flex-shrink: 0;
    width: 36px;
  }

  &__forgot {
    color: $loginFormColor;
    font-size: 10px;
  }

  .form-group {
    position: relative;
    margin: 10px 15px;
    border-radius: 3px;

    &::before,
    &::after {
      position: absolute;
      display: block;
      content: "";
      background-image: url(../img/icons.png);

      @media (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi) {
        background-image: url(../img/icons@2x.png);
        background-size: 476px 452px;
      }
    }

    &::before {
      background-color: $loginFormColor;
    }

    &::after {
      display: none;
      background-position: -459px -278px;
      width: 15px;
      height: 12px;
      right: 12px;
      top: 12px;
    }

    .sp-icons {
      margin: auto;
    }

    &.has-success {
      &::before {
        background-color: $linkReg;
        border-color: $linkReg;
      }

      &::after {
        display: block;
      }

      .form-control {
        border-color: $linkReg;
        background-color: #e2e2e2;
      }
    }

    &.has-error {
      &::before {
        background-color: #ce6464;
        border-color: #ce6464;
      }

      .form-control {
        border-color: transparent;
        background-color: #ffcece;
      }
    }

    .help-block {
      font-size: 11px;
    }

    .hint-block,
    .control-label {
      display: none;
    }
  }

  .form-control {
    padding: 9px 38px 8px 44px;
    font-size: 14px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid transparent;
    border-radius: 0 2px 2px 0;
    background: #f1f1f1;

    &:focus {
      outline: none;
      border-color: $loginFormColor;
    }


    &[type=submit] {
      font-size: 12px;
      font-weight: 300;
      padding: 5px 15px 5px 30px;
      background-color: $submitBg;
      border-radius: 2px;
      width: 100%;

      &:hover {
        background-color: lighten($submitBg, 5%);
      }
    }
  }

  &__submit {
    margin: 15px;

    .form-group {
      margin: 0px;
    }

    .form-control {
      font-weight: 700;
      padding: 10px 15px;
      color: white;
    }
  }
}

.lang-drop {
  width: auto;
  background-color: white;
  padding: 2px;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  font-size: 14px;
  border: 1px solid #dedede;


  &__link {
    margin: 4px;
    padding: 2px 5px;
    color: black;

    &:hover,
    &_current {
      color: white;
      background-color: #7fb2d9;
    }
  }

  &__i {
    margin-right: 5px;
  }
}

.custom-login-field {

  &::before {    
    background-position: -366px -110px;
    width: 19px;
    height: 19px;
    border: 8px solid $loginFormColor;
    border-radius: 2px 0 0 2px;
  }
}

.custom-password-field {

  &::before {    
    background-position: -459px -262px;
    width: 12px;
    height: 16px;
    border-left: 12px solid $loginFormColor;
    border-right: 11px solid $loginFormColor;
    border-top: 9px solid $loginFormColor;
    border-bottom: 10px solid $loginFormColor;
    border-radius: 2px 0 0 2px;
  }
}

.custom-phone-field {

  &::before {    
    background-position: -386px -348px;
    width: 19px;
    height: 18px;
    border-left: 8px solid #919191;
    border-right: 8px solid #919191;
    border-top: 9px solid #919191;
    border-bottom: 8px solid #919191;
    border-radius: 2px 0 0 2px;
  }
}

.custom-mail-field {

  &::before {    
    background-position: -405px -348px;
    width: 22px;
    height: 14px;
    border-left: 7px solid #919191;
    border-right: 7px solid #919191;
    border-top: 11px solid #919191;
    border-bottom: 10px solid #919191;
    border-radius: 2px 0 0 2px;
  }
}

.form-control.custom-remember {
  width: auto;
  margin-right: 5px;
}

.form-group.custom-submit {

  &::after {
    display: block;
    background-position: -459px -290px;
    left: 9px;
    top: 7px;
  }
}

.link-reg {
  position: relative;
  background-color: $linkReg;
  margin-top: 15px;

  &__link {
    color: white;
    padding: 10px 15px;
    display: block;

    &:hover {
      color: white;
      background-color: lighten($linkReg, 2%);
      opacity: 0.7;
    }
  }

  &::after {
    background-image: url(../img/icons.png);
    position: absolute;
    display: block;
    content: "";
    background-position: -200px -110px;
    width: 23px;
    height: 23px;
    right: 8px;
    top: 8px;

    @media (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi) {
      background-image: url(../img/icons@2x.png);
      background-size: 476px 452px;
    }

  }
}


// 

.modal {
  width: 340px;
  padding: 0;
  background-color: #fafafa;
  border-radius: $modalRadius;
  font-family: Roboto, sans-serif;

  &__footer {
    background-color: #ededed;
    height: 55px;
    border-radius: 0 0 $modalRadius $modalRadius;
  }

  &__footer-link {
    color: #212121;
    margin: 10px;

    &:hover {
      color: lighten(#212121, 10%);
    }

    &_main {
      font-weight: 500;
    }
  }

  &__close {
    position: absolute;
    right: 0;
    font-size: 15px;
    color: white;
    opacity: 0.5;
    padding: 5px 10px;
    cursor: pointer;

    &:hover {
      color: white;
      opacity: 0.7;
    }
  }

  @media (max-width: $s) {
    overflow: auto;
  }
}

.login-form {
  &__input-item {
    margin: 20px 30px;
  }
}

.custom-div {
  position: relative;


  &.has-success {
    .custom-input {
      border: 2px solid $brightBg;
    }
  }

  &.has-error {
    .custom-input {
      border: 2px solid $submitBg;
    }
  }

  &::after {
    position: absolute;
    display: block;
    content: "";
    right: 10px;
    top: 7px;
    @extend .sp-iconsm;
  }

  &_login {
    &::after {
      position: absolute;
      display: block;
      content: "";
      @extend .sp-iconsm__mail;
    }
  }

  &_pass {
    &::after {
      position: absolute;
      display: block;
      content: "";
      @extend .sp-iconsm__lock;
    }
  }

  &_phone {
    &::after {
      position: absolute;
      display: block;
      content: "";
      @extend .sp-iconsm__skype;
    }
  }
}

.custom-input {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  border-radius: 3px;
  border: 2px solid #dfdfdf;

  &:focus {
    border: 2px solid #BFBFBF;
    outline: none;
  }
}

.custom-submit {
  padding: 4px;
  height: auto;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 500;
  text-transform: none;
  width: 100%;
  box-sizing: border-box;
}

.custom-checkbox {
  position: relative;
  padding-left: 10px;

  &_checked {
    &::before {
      @extend .sp-icons__checked;
    }
  }

  &_unchecked {
    &::before {
      @extend .sp-icons__unchecked;
    }
  }

  [type="checkbox"] {
    opacity: 0;
  }

  &::before {
    position: absolute;
    display: block;
    content: "";
    left: 0;
    @extend .sp-icons;
  }
}

//    

@media (max-width: $m - 1) {
  [data-uk-scrollspy*=uk-animation-]:not([data-uk-scrollspy*=target]) {
    opacity: 1!important;
    animation-name: none!important;
  }
}
